import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HierarchyService {

  constructor() { }

  public formatOrg(hierarchy): any {
    const rootNode = this.getRootNode(hierarchy);
    return {
      ...rootNode,
      subNodes: this.findChildren(rootNode, hierarchy)
    };
  }

  private findChildren(root, hierarchy): any {
    const children = hierarchy.filter(item => item.parent_id === root.id);

    return children.map(item => {
      return {
        ...item,
        subNodes: this.findChildren(item, hierarchy)
      };
    });
  }

  private getRootNode(hierarchy): any {
    return hierarchy.filter(item => !item.parent_id)[0];
  }
}
